<template>
   <div class="login-container" style="height: 100vh;">
     <div>
       <el-card header="" class="login-card">
      <el-form  @submit.native.prevent="login">
        <el-form-item label="" >
          <el-input type="text" v-model="model.username"></el-input>
        </el-form-item>
        <el-form-item label=""  >
          <el-input type="password" v-model="model.password"></el-input>
        </el-form-item>
         <el-form-item>
           <p style="color:red;">* admin     123456</p>
          <el-button type="primary" native-type="submit"></el-button>
          
        </el-form-item>
      </el-form>
    </el-card>
     </div>
    
  </div>
</template>

<script>
export default {
     data(){
    return{
      model:{}
    }
  },
  methods:{
     async login(){
     const res=await this.$http.post('login',this.model);
     //token sessionStorage
     localStorage.token=res.data.token
     //token localStorage.clear
     this.$router.push('/home')
     this.$message({
       type:'success',
       message:''
     })
    }
  }
}
</script>
<style>
.login-container{
  background-color: rgb(160, 226, 226);
  padding: 5rem 0 0 25rem;
}
.login-card{
  width:25rem;
 
  }
</style>