<template>
<div>
  <h1 style="margin-top:0px;"></h1>
    <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" height="450" style="width: 100%">
       <el-table-column type="index" width="50" label="#"></el-table-column>
       <el-table-column prop="parent.name" label=""></el-table-column>
       <el-table-column prop="name" label=""></el-table-column>
     <!--  <el-table-column prop="images[0]" label=""></el-table-column>-->
      <el-table-column
      label="">
      <template slot-scope="scope">
    <el-tag v-if="scope.row.difficulty == 1" ></el-tag>  
    <el-tag v-else-if="scope.row.difficulty == 2" type="success"></el-tag>
    <el-tag v-else-if="scope.row.difficulty == 3" type="info"></el-tag>
    <el-tag v-else-if="scope.row.difficulty == 4" type="warning"></el-tag>
    <el-tag v-else-if="scope.row.difficulty == 5" type="danger"></el-tag>
  </template>
    </el-table-column>
       <el-table-column prop="time" label=""></el-table-column>
       <el-table-column label="" fixed="right">
         <template slot-scope="scope">
           <el-button size="mini" @click="$router.push(`/menus/edit/${scope.row._id}`)"></el-button>
           <el-button size="mini" type="danger" @click="remove(scope.row)"></el-button>
         </template>
       </el-table-column>
    </el-table>
    <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]" 
          :page-size="pagesize"         
          layout="total, sizes, prev, pager, next, jumper"
          :total="tableData.length">    //
      </el-pagination>
</div>
</template>
<style>
.el-table-column{
  width:100px;
}
.el-pagination{
  margin:20px 0 0 200px;
}
</style>
<script>
export default {
  
  data() {
      return {
        tableData: [],
        currentPage:1, //
        pagesize:5,    //    
        select:''
      }
    },
    methods:{

      async fetch(){
        const res=await this.$http.get(`rest/menus/`)
        this.tableData=res.data
        
      },
     
      async remove(row){
            this.$confirm(`"${row.name}"?`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(async() => {
            await this.$http.delete(`rest/menus/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch() 
        });
        },
        // currentPagepagesizedata
        handleSizeChange: function (size) {
                this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
        },
    },
   created(){
     this.fetch();
    }
}
</script>