<template>
<div>
  <h1 style="margin-top:0px;"></h1>
    <el-table :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)" height="450" style="width: 100%">
       <el-table-column fixed prop="_id" label="ID"></el-table-column>
       <el-table-column prop="username" label=""></el-table-column>
       <el-table-column prop="avatar" label=""></el-table-column>
       <el-table-column label="" fixed="right">
         <template slot-scope="scope">
           <el-button size="mini" @click="$router.push(`users/edit/${scope.row._id}`)"></el-button>
           <el-button size="mini" type="danger" @click="remove(scope.row)"></el-button>
         </template>
       </el-table-column>
    </el-table>
    <el-pagination
          background
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[5, 10, 20, 40]" 
          :page-size="pagesize"         
          layout="total, sizes, prev, pager, next, jumper"
          :total="tableData.length">    //
      </el-pagination>
</div>
</template>
<style>
.el-table-column{
  width:120px;
}
</style>
<script>
export default {
  data() {
      return {
        tableData: [],
        currentPage:1, //
        pagesize:5,    //    
      }
    },
    methods:{
      handleSizeChange: function (size) {
                this.pagesize = size;
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
        },
      async fetch(){
        const res=await this.$http.get(`rest/users/`)
        this.tableData=res.data
      },
      async remove(row){
            this.$confirm(`?"${row.name}"`, '', {
            confirmButtonText: '',
            cancelButtonText: '',
            type: 'warning'
        }).then(async() => {
            await this.$http.delete(`rest/users/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch() 
        });
        }
    },
   created(){
     this.fetch();
    }
}
</script>